@use "../utilities" as *;

/* scroll-to-top */
.progress-wrap {
  position: fixed;
  right: 60px;
  bottom: 60px;
  height: 60px;
  width: 60px;
  cursor: pointer;
  display: block;
  padding: 8px;
  border-radius: 100%;
  background-color: var(--bs-white);
  box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.05), 0px 1px 1px 0px rgba(0, 0, 0, 0.03), 0px 3px 5px 0px rgba(0, 0, 0, 0.03);
  z-index: 1010;
  opacity: 0;
  visibility: hidden;
  transform: translateY(15px);
  transition: all 200ms linear;
  @include media-breakpoint-down(xl) {
    right: 45px;
    bottom: 45px;
  }
  @include media-breakpoint-down(lg) {
    right: 30px;
    bottom: 30px;
  }
  @include media-breakpoint-down(md) {
    right: 25px;
    bottom: 25px;
  }
  @include media-breakpoint-down(sm) {
    right: 20px;
    bottom: 20px;
  }

  &.active-progress {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }

  &::after,
  &::before {
    position: absolute;
    font-family: uicons-regular-rounded;
    content: "\e0d2";
    text-align: center;
    line-height: 60px;
    font-size: 24px;
    left: 0;
    top: 0;
    height: 60px;
    width: 60px;
    cursor: pointer;
    display: block;
    transition: all 200ms linear;
  }

  &::after {
    color: var(--wc-primary);
    z-index: 1;
  }

  &:hover::after {
    opacity: 0;
  }

  &::before {
    opacity: 0;
    z-index: 2;
  }

  &:hover::before {
    opacity: 1;
  }

  &.lightScrollIcon::after {
    color: #ecedf3 !important;
  }

  svg path {
    fill: none;
  }

  svg.progress-circle path {
    stroke: var(--wc-primary);
    stroke-width: 4;
    box-sizing: border-box;
    transition: all 200ms linear;
  }
}
