.countdown-container {
  gap: 12px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 40px;
  border-radius: 100px;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
  h1 {
    font-size: 50px;
    margin-bottom: 10px;
    font-family: var(--wc-ff-number);
  }
  p {
    font-size: 18px;
    margin-bottom: 40px;
  }
  .circle {
    width: 120px !important;
    height: 120px !important;
    position: relative !important;
  }
  .circle svg {
    transform: rotate(-90deg);
    width: 100%;
    height: 100%;
  }
  .circle circle {
    fill: none;
    stroke-width: 3;
    stroke-linecap: round;
  }
  .circle .bg {
    stroke: #e3f1ee;
  }
  .circle .progress {
    stroke: var(--wc-primary);
    stroke-dasharray: 314; /* 2πr (r=50) */
    stroke-dashoffset: 314;
    transition: stroke-dashoffset 0.5s ease;
  }
  .time-label {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 25px;
    font-weight: 800;
    color: var(--bs-primary);
    background-color: #f7fffd;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    box-shadow: var(--wc-shadow-lg);
    line-height: 1.5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .time-unit {
    font-size: 11px;
    display: block;
    font-weight: 700;
    text-transform: uppercase;
    color: var(--bs-secondary);
  }
}
